import uniRate from '@/components/uni-rate/uni-rate.vue'
import sxRate from '@/components/sx-rate/index.vue'

export default {
	name: "",
	components: {
		uniRate,
		sxRate
	},
	props: {},
	data() {
		return {
			movieDetails: {},
			roleList: [],
			plotPics:[]
		}
	},
	methods: {
		lokeImg(url,index) {
			// 预览图片
			uni.previewImage({
				urls: url,
				current:index,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						// console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},

		//查询演职人员
		getRole(id, role) {
			uni.showLoading()
			uni.request({
				url: `https://www.imovietrailer.com/superhero/search/staff/${id}/${role}?qq=2684425594`,
				method: 'POST',
				data: {
					trailerId: id,
					role: role

				},
				header: {
					'custom-header': 'application/json' //自定义请求头信息
				},
				success: res => {
					if (res.statusCode === 200) {

						this.roleList = this.roleList.concat(res.data.data)
						// console.log(this.movieDetails)
					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			})
		},
		// 获取电影详细信息
		getULike(id) {
			uni.showLoading()
			uni.request({
				url: `https://www.imovietrailer.com/superhero/search/trailer/${id}?qq=2684425594`,
				method: 'POST',
				data: {
					trailerId: id,

				},
				header: {
					'custom-header': 'application/json' //自定义请求头信息
				},
				success: res => {
					if (res.statusCode === 200) {
						this.movieDetails = res.data.data
						this.plotPics=JSON.parse(this.movieDetails.plotPics)

					}
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading()
				}
			})
		}
	},
	// beforeMount() {
	// 	this.getULike()
	// },
	mounted() {

	},
	onLoad(option) {
		this.getULike(option.id)
		this.getRole(option.id, 1)
		this.getRole(option.id, 2)
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
